.NewTodoForm {
  display: flex;
  flex-wrap: wrap;
  margin-top: 3rem;
}

.NewTodoForm label {
  min-width: 100%;
  margin-bottom: 0.5rem;
  font-size: 1.3rem;
}

.NewTodoForm input {
  flex-grow: 1;
  border: none;
  background: #f7f1f1;
  padding: 0 1.5em;
  font-size: initial;
}

.NewTodoForm button {
  padding: 0 1.3rem;
  border: none;
  background: #ff6666;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid #ffffff4d;
  margin-left: 5px;
  cursor: pointer;
  transition: background 0.2 ease-out;
}

.NewTodoForm button:hover {
  background: #ff5e5e;
}

.NewTodoForm input,
.NewTodoForm button {
  font-family: "Quicksand", sans-serif;
  height: 3rem;
}